<template>
  <div class="section">
    <div class="tabs">
      <div :class="{active:tab == 1}" @click="tabs(1)">水平投影图</div>
      <div :class="{active:tab == 2}" @click="tabs(2)">垂直剖面图</div>
      <div :class="{active:tab == 3}" @click="tabs(3)">三维轨迹图</div>
    </div>
    <div class="main" id="char"></div>
  </div>
</template>

<script>
  import echarts from 'echarts'
  import e3d from 'echarts-gl'
  import request from "@/utils/request";

  export default {
    props: ["route"],
    data() {
      return {
        tab: 1,
        char: {},
        //   zuoBiao: new Array(5)
        zuoBiao: [
          [],
          [],
          [],
          [],
          []    //[[南北,东西，井深],...]
        ]
      }
    },

    components: {},

    computed: {},

    mounted: function () {
      let data = this.route;
      this.char = echarts.init(document.getElementById('char'));
      request({
        url: '/data/query/trajectoryprofiledata',
        method: 'post',
        data
      }).then(res => {
        res.forEach(item => {
          this.zuoBiao[0].push(item.nourth_south);      //南北
          this.zuoBiao[1].push(item.east_west);         //东西
          this.zuoBiao[2].push(item.depth);             //井深
          this.zuoBiao[3].push(item.vertical_depth);    //垂深
          this.zuoBiao[4].push([item.nourth_south, item.east_west, item.depth])     // [[南北,东西，井深],...]
        });

        //模拟数据
        // this.zuoBiao[4][0][0] =2;
        // this.zuoBiao[4][0][1] =6;
        // this.zuoBiao[4][4][0] =8;
        // this.zuoBiao[4][4][1] =6;
        this['draw' + 1](this.zuoBiao)
      });
    },

    methods: {
      tabs(i) {
        if (this.tab == i) return;
        this.tab = i;
        this.char.clear()
        this['draw' + i](this.zuoBiao)
      },

      draw1(data) {
        this.char.setOption({
          title: {
            text: '水平投影图'
          },
          tooltip : {
            trigger: 'item'
          },
          xAxis: {
            name: '井深',
            nameLocation: 'end',
            type: 'category',
            data: data[2]
          },
          yAxis: {
            name: '垂深',
            nameLocation: 'end',
            type: 'value'
          },
          series: [{
            data: data[3],
            type: 'line'
          }]
        })
      },
      draw2(data) {
        this.char.setOption({
          title: {
            text: '垂直剖面图'
          },
          tooltip : {
            trigger: 'item'
          },
          xAxis: {
            name: '南北',
            nameLocation: 'end',
            type: 'category',
            data: data[0]
          },
          yAxis: {
            name: '东西',
            nameLocation: 'end',
            type: 'value'
          },
          series: [{
            data: data[1],
            type: 'line',
          }]
        })
      },
      draw3(data) {
        this.char.setOption({
          tooltip : {
            trigger: 'item'
          },
          backgroundColor: '#fff',
          visualMap: {
            show: false,
            dimension: 2,
            min: 0,
            max: 30,
            inRange: {
              color: ['#313695', '#4575b4', '#74add1', '#abd9e9', '#e0f3f8', '#ffffbf', '#fee090', '#fdae61',
                '#f46d43', '#d73027', '#a50026'
              ]
            }
          },
          xAxis3D: {
            name: '南北',
            type: 'value'
          },
          yAxis3D: {
            name: '东西',
            type: 'value'
          },
          zAxis3D: {
            name: '井深',
            type: 'value'
          },
          grid3D: {
            viewControl: {
              projection: 'orthographic'
            }
          },
          series: [{
            type: 'line3D',
            data: data[4],
            lineStyle: {
              width: 4
            },
          }]
        })
      },
    }
  }

</script>

<style lang="scss" scoped>
  #char {
    height: calc(100% - 50px);
    background: #fff;
  }

</style>



